import React from 'react';

import international from './internationallogo.svg';

import HeaderTitle from '../HeaderTitle';
import Flex from '../../components/Flex';
import Text from '../../components/Text';
import Box from '../../components/Box';
import BackgroundImage from '../../components/BackgroundImage';

const Boxtext = ({children, ...props}) => (
  <Box
    fontSize={['1em', null, '1.25em']}
    borderBottom="1.5px solid"
    borderColor="darkBlue"
    pb="0.5em"
    my="0.75em"
    color="black"
    fontWeight="bold"
    {...props}
  >
    {children}
  </Box>
);

const Numbertext = ({children, ...props}) => (
  <Text.inline fontSize="1.5em" color="darkBlue" mx="0.25em" {...props}>{children}</Text.inline>
)

const Picture = (props) => (
  <Box width={1} mx={['0', null, '1em']} mt={['2.5em', null, '0']} {...props}>
    <BackgroundImage src={international} ratio={786.62 / 748.73} />
  </Box>
)

const Word = () => (
  <Box width={1} mx={['0', null, '1em']} my="auto">
    <Box bg="white" px="2em" py="2em">
      <Boxtext><Numbertext>290</Numbertext></Boxtext>
      <Boxtext><Numbertext>31</Numbertext></Boxtext>
      <Boxtext><Numbertext>31</Numbertext></Boxtext>
      <Boxtext><Numbertext>123</Numbertext></Boxtext>
      <Boxtext><Numbertext>10.8</Numbertext></Boxtext>
    </Box>
    <Text fontSize="0.75em" mt="1em"><b></b>Fresh Taiwan2012-2018</Text>
  </Box>
)

const International = (...props)=> {
  return (
    <Box py="3em" color="darkBlue" {...props}>
      <HeaderTitle index="4" lineBreak />
      <Flex mx="10%" flexWrap={['wrap', null, 'nowrap']}>
        <Picture display={['none', null, 'block']} />
        <Word />
        <Picture display={['block', null, 'none']} />
      </Flex>
    </Box>
  );
};

export default International;
